'use client';

import { useState, useRef, useEffect } from 'react';
import { Button } from '@/components/ui/Button';
import { Card } from '@/components/ui/Card';
import { Input } from '@/components/ui/Input';
import { 
  MessageSquare, 
  Send, 
  Bot, 
  User, 
  X,
  Minimize2,
  Maximize2
} from 'lucide-react';

interface ChatMessage {
  id: string;
  content: string;
  sender: 'user' | 'ai';
  timestamp: Date;
  context?: string;
}

interface AIChatProps {
  reportId?: string;
  sessionId?: string;
  onClose?: () => void;
  isMinimized?: boolean;
  onToggleMinimize?: () => void;
}

export function AIChat({ 
  reportId, 
  sessionId, 
  onClose, 
  isMinimized = false, 
  onToggleMinimize 
}: AIChatProps) {
  const [messages, setMessages] = useState<ChatMessage[]>([
    {
      id: '1',
      content: "Hi! I'm here to help clarify your interview feedback. What would you like to know more about?",
      sender: 'ai',
      timestamp: new Date()
    }
  ]);
  const [inputValue, setInputValue] = useState('');
  const [isLoading, setIsLoading] = useState(false);
  const messagesEndRef = useRef<HTMLDivElement>(null);

  const scrollToBottom = () => {
    messagesEndRef.current?.scrollIntoView({ behavior: 'smooth' });
  };

  useEffect(() => {
    scrollToBottom();
  }, [messages]);

  const handleSendMessage = async () => {
    if (!inputValue.trim() || isLoading) return;

    const userMessage: ChatMessage = {
      id: Date.now().toString(),
      content: inputValue,
      sender: 'user',
      timestamp: new Date()
    };

    setMessages(prev => [...prev, userMessage]);
    setInputValue('');
    setIsLoading(true);

    // Simulate AI response
    setTimeout(() => {
      const aiResponse: ChatMessage = {
        id: (Date.now() + 1).toString(),
        content: generateAIResponse(inputValue),
        sender: 'ai',
        timestamp: new Date()
      };
      setMessages(prev => [...prev, aiResponse]);
      setIsLoading(false);
    }, 1500);
  };

  const generateAIResponse = (userInput: string): string => {
    const input = userInput.toLowerCase();
    
    // Analyze user's context from previous messages
    const conversationContext = messages.slice(-5).map(m => m.content.toLowerCase()).join(' ');
    
    // Score-related questions
    if (input.includes('score') || input.includes('rating') || input.includes('performance')) {
      if (input.includes('confidence')) {
        return "Your confidence score reflects how assured and self-assured you sounded during the interview. A higher confidence score indicates you spoke with conviction, maintained good pace, and avoided excessive filler words like 'um' and 'uh'. To improve confidence: practice your responses out loud, maintain good posture, and speak at a measured pace. Would you like specific exercises to boost your confidence?";
      } else if (input.includes('clarity')) {
        return "Your clarity score measures how well you articulated your thoughts and how easy you were to understand. This includes your pronunciation, sentence structure, and logical flow of ideas. To improve clarity: practice the STAR method (Situation, Task, Action, Result), organize your thoughts before speaking, and use specific examples. What specific area of clarity would you like to work on?";
      } else if (input.includes('relevance')) {
        return "Your relevance score indicates how well your answers addressed the specific questions asked and how applicable your examples were to the role. To improve relevance: listen carefully to each question, ask for clarification if needed, and choose examples that directly demonstrate the skills being assessed. Which questions felt most challenging to answer relevantly?";
      } else if (input.includes('structure')) {
        return "Your structure score reflects how well-organized your responses were. Good structure means having a clear beginning, middle, and end to your answers. The STAR method is excellent for this: Situation (context), Task (what needed to be done), Action (what you did), Result (the outcome). Would you like me to help you restructure any of your responses using this method?";
      } else {
        return "I can help you understand any of your interview scores in detail. Your scores cover four key areas: Confidence (how assured you sounded), Clarity (how well you articulated ideas), Relevance (how well you answered the questions), and Structure (how organized your responses were). Which specific score would you like me to explain in more detail?";
      }
    }
    
    // Improvement and tips
    if (input.includes('improve') || input.includes('better') || input.includes('tips')) {
      if (input.includes('technical') || input.includes('coding')) {
        return "For technical interviews, here's a structured approach: 1) Clarify the problem requirements and ask questions, 2) Discuss your high-level approach before coding, 3) Write clean, readable code with good variable names, 4) Test your solution with examples, 5) Discuss time/space complexity, 6) Consider edge cases and optimizations. Practice coding problems daily and explain your thinking process out loud. What specific technical areas would you like to focus on?";
      } else if (input.includes('behavioral')) {
        return "For behavioral questions, use the STAR method consistently: Situation (set the context), Task (explain what needed to be accomplished), Action (describe what YOU specifically did), Result (share the outcome and what you learned). Choose examples that showcase leadership, problem-solving, teamwork, and growth mindset. Quantify your impact when possible (e.g., 'reduced processing time by 30%'). Which behavioral competency would you like to practice?";
      } else if (input.includes('communication')) {
        return "To improve communication: 1) Pause instead of using filler words, 2) Structure your thoughts before speaking, 3) Use the 'Rule of 3' - make three key points, 4) Practice active listening and ask clarifying questions, 5) Use specific examples and metrics, 6) Maintain eye contact and confident body language. Would you like specific exercises to practice any of these areas?";
      } else {
        return "Based on your interview performance, I recommend focusing on: 1) Using the STAR method for structured responses, 2) Practicing specific examples that demonstrate your skills, 3) Improving your pace and reducing filler words, 4) Preparing questions that show your interest in the role. What's the most important area you'd like to work on first?";
      }
    }
    
    // Filler words and speech patterns
    if (input.includes('filler') || input.includes('um') || input.includes('uh') || input.includes('like')) {
      return "Reducing filler words takes practice but makes a huge difference in how confident you sound. Here are proven techniques: 1) Pause and breathe instead of saying 'um' - silence is better than fillers, 2) Slow down your speech pace, 3) Practice your key stories until they're natural, 4) Record yourself and count fillers to build awareness, 5) Use transition phrases like 'That's a great question' to buy thinking time. Try the '3-second rule' - pause for 3 seconds before answering. Would you like specific practice exercises?";
    }
    
    // Specific interview types
    if (input.includes('system design')) {
      return "For system design interviews: 1) Start by clarifying requirements and scale (users, data, requests/second), 2) Estimate capacity needs, 3) Design high-level architecture with major components, 4) Deep dive into specific components, 5) Discuss data storage and retrieval, 6) Address scalability, reliability, and performance, 7) Consider monitoring and security. Always think out loud and ask questions. What type of system would you like to practice designing?";
    }
    
    // Nervousness and anxiety
    if (input.includes('nervous') || input.includes('anxiety') || input.includes('stress')) {
      return "Interview nerves are completely normal! Here are strategies that work: 1) Practice your key stories until they're second nature, 2) Do mock interviews with friends or record yourself, 3) Research the company and role thoroughly, 4) Prepare thoughtful questions to ask them, 5) Use power poses before the interview, 6) Focus on breathing deeply and speaking slowly, 7) Remember that they want you to succeed. The more you practice, the more confident you'll feel. What specific aspect of interviews makes you most nervous?";
    }
    
    // Company research and preparation
    if (input.includes('research') || input.includes('prepare') || input.includes('company')) {
      return "Thorough preparation makes a huge difference! Research: 1) Company mission, values, and recent news, 2) The specific role requirements and how you fit, 3) Your interviewer's background on LinkedIn, 4) Common interview questions for this role/company, 5) Prepare 3-5 STAR method stories that showcase different skills, 6) Prepare thoughtful questions about the role, team, and company culture. Show genuine interest and curiosity. What specific company or role are you preparing for?";
    }
    
    // Follow-up questions and engagement
    if (input.includes('question') || input.includes('ask them')) {
      return "Great questions show your interest and help you evaluate the opportunity: 1) 'What does success look like in this role after 6 months?' 2) 'What are the biggest challenges facing the team right now?' 3) 'How would you describe the team culture and collaboration style?' 4) 'What opportunities are there for professional development?' 5) 'What do you enjoy most about working here?' Avoid questions about salary, benefits, or vacation time in early rounds. What aspects of the role are you most curious about?";
    }
    
    // Context-aware responses based on conversation history
    if (conversationContext.includes('score') && (input.includes('how') || input.includes('what'))) {
      return "Since we were discussing your scores, let me provide more specific guidance. Each score reflects different aspects: Confidence shows in your tone and pace, Clarity in your word choice and organization, Relevance in how well you answered the actual question, and Structure in your logical flow. Which of these areas would you like actionable steps to improve?";
    }
    
    // Default helpful response
    return "I'm here to help you improve your interview skills! I can provide specific guidance on: üìä Understanding your performance scores, üí° Improvement strategies for different interview types, üó£Ô∏è Communication and presentation tips, üìù Using the STAR method effectively, üß† Handling technical questions, üíº Behavioral interview techniques, üò∞ Managing interview nerves. What specific area would you like to focus on?";
  };

  if (isMinimized) {
    return (
      <div className="fixed bottom-4 right-4 z-50">
        <Button
          onClick={onToggleMinimize}
          className="rounded-full w-12 h-12 bg-blue-600 hover:bg-blue-700 shadow-lg"
        >
          <MessageSquare className="w-6 h-6" />
        </Button>
      </div>
    );
  }

  return (
    <Card className="fixed bottom-4 right-4 w-96 h-96 z-50 flex flex-col shadow-xl">
      {/* Header */}
      <div className="flex items-center justify-between p-4 border-b">
        <div className="flex items-center">
          <Bot className="w-5 h-5 text-blue-600 mr-2" />
          <h3 className="font-semibold">AI Assistant</h3>
        </div>
        <div className="flex items-center space-x-2">
          {onToggleMinimize && (
            <Button variant="secondary" size="sm" onClick={onToggleMinimize}>
              <Minimize2 className="w-4 h-4" />
            </Button>
          )}
          {onClose && (
            <Button variant="secondary" size="sm" onClick={onClose}>
              <X className="w-4 h-4" />
            </Button>
          )}
        </div>
      </div>

      {/* Messages */}
      <div className="flex-1 overflow-y-auto p-4 space-y-4">
        {messages.map((message) => (
          <div
            key={message.id}
            className={`flex ${message.sender === 'user' ? 'justify-end' : 'justify-start'}`}
          >
            <div
              className={`max-w-[80%] p-3 rounded-lg ${
                message.sender === 'user'
                  ? 'bg-blue-600 text-white'
                  : 'bg-gray-100 text-gray-900'
              }`}
            >
              <div className="flex items-start space-x-2">
                {message.sender === 'ai' && (
                  <Bot className="w-4 h-4 mt-0.5 flex-shrink-0" />
                )}
                {message.sender === 'user' && (
                  <User className="w-4 h-4 mt-0.5 flex-shrink-0" />
                )}
                <div>
                  <p className="text-sm">{message.content}</p>
                  <p className={`text-xs mt-1 ${
                    message.sender === 'user' ? 'text-blue-100' : 'text-gray-500'
                  }`}>
                    {message.timestamp.toLocaleTimeString()}
                  </p>
                </div>
              </div>
            </div>
          </div>
        ))}
        
        {isLoading && (
          <div className="flex justify-start">
            <div className="bg-gray-100 p-3 rounded-lg">
              <div className="flex items-center space-x-2">
                <Bot className="w-4 h-4" />
                <div className="flex space-x-1">
                  <div className="w-2 h-2 bg-gray-400 rounded-full animate-bounce"></div>
                  <div className="w-2 h-2 bg-gray-400 rounded-full animate-bounce" style={{ animationDelay: '0.1s' }}></div>
                  <div className="w-2 h-2 bg-gray-400 rounded-full animate-bounce" style={{ animationDelay: '0.2s' }}></div>
                </div>
              </div>
            </div>
          </div>
        )}
        <div ref={messagesEndRef} />
      </div>

      {/* Input */}
      <div className="p-4 border-t">
        <div className="flex space-x-2">
          <Input
            value={inputValue}
            onChange={(e) => setInputValue(e.target.value)}
            placeholder="Ask about your feedback..."
            onKeyPress={(e) => e.key === 'Enter' && handleSendMessage()}
            disabled={isLoading}
          />
          <Button
            onClick={handleSendMessage}
            disabled={!inputValue.trim() || isLoading}
            size="sm"
          >
            <Send className="w-4 h-4" />
          </Button>
        </div>
      </div>
    </Card>
  );
}